import React from "react";
import classNames from "./FormInput.module.css";

const FormInput = ({ i }) => {
  return (
    <div className={classNames.container}>
      <label htmlFor={i.id}>{i.label}</label>
      <input
        type={i.type}
        placeholder={i.placeholder}
        required={i.required}
        id={i.id}
        onChange={(e) => i.setFn(e.target.value)}
      />
    </div>
  );
};

export default FormInput;
